.App {
  text-align: center;
  font-size: 1em;
}

#logo:hover {
  -webkit-animation: spin 1.5s infinite linear;
          animation: spin 1.5s infinite linear;
}

button {
  background-color: #FF4B2A;
  color: white;
  padding: 10px;
  border: none;
  font-weight: bold;
  cursor: pointer;
  border-radius: 20px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

button:hover {
  background-color: #D52100;
}

#titleDiv {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding-bottom: 8px;
  padding-top: 5px;
}


#title {
  font-weight: bold;
  -ms-flex-item-align: center;
      align-self: center;
  font-size: 40px;
}

.name {
  color: #ffff1c;
  font-size: 25px;
  margin: 0px;
  margin-top: -15px;
  font-weight: bold;
}

strong {
  color: #ffff1c;
}

#drawer {
  width: 250px;
  color: black;
}

#loading {
    margin: 20px;
    width: 100px; 
    height: 100px;    
    -webkit-animation: spin 1.5s infinite linear;    
            animation: spin 1.5s infinite linear;
}

@-webkit-keyframes spin {
    0%  {-webkit-transform: rotate(0deg);}
    100% {-webkit-transform: rotate(360deg);}   
}

#photo {
  border-radius: 15px;
  -webkit-box-shadow: 8px 8px 5px #0084B0;
          box-shadow: 8px 8px 5px #0084B0;
  margin-bottom: -8px;
}

@-webkit-keyframes fadeIn {
  from {opacity: 0;}
  to {opacity: 100;}
}

@keyframes fadeIn {
  from {opacity: 0;}
  to {opacity: 100;}
}

.info {
  -webkit-animation-name: fadeIn;
          animation-name: fadeIn;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

#buttonDiv button {
  border-radius: 0px
}

#directions {
    background-color: #0084B0;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

#directions:hover {
    background-color: #006485;
}

#savedRests {
  color: black;
  font-weight: bold;
  text-align: center;
}

.restList {
  cursor: pointer;
    background-color: white;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.restList:hover {
    background-color: #bababa;
}

.blueButton {
    background-color: #0084B0;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.blueButton:hover {
    background-color: #006485;
}



